.connect4-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.header h1 {
  margin: 0;
  color: #2c3e50;
}

.account-info {
  padding: 8px 12px;
  background-color: #ecf0f1;
  border-radius: 4px;
  font-size: 14px;
  color: #34495e;
}

.message-bar {
  background-color: #f8f9fa;
  border-left: 4px solid #3498db;
  padding: 12px;
  margin-bottom: 20px;
  border-radius: 4px;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.close-button {
  background: none;
  border: none;
  color: #7f8c8d;
  font-size: 20px;
  cursor: pointer;
  padding: 0 5px;
}

.game-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.game-controls {
  width: 100%;
  margin-bottom: 20px;
  text-align: center;
}

.game-status {
  margin: 15px 0;
  font-size: 18px;
  font-weight: 500;
}

.button-container {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 20px;
}

.loading-overlay {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 300px;
}

.loading-spinner {
  border: 4px solid #f3f3f3;
  border-top: 4px solid #3498db;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
  margin-bottom: 20px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.error-container {
  background-color: #ffdddd;
  border-left: 6px solid #f44336;
  padding: 20px;
  border-radius: 5px;
  margin: 20px 0;
}

.error-container h2 {
  color: #d32f2f;
  margin-top: 0;
}

.error-container button {
  margin-top: 15px;
}