.game-board {
  margin: 20px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.column-buttons {
  display: flex;
  width: 490px;
  justify-content: space-around;
  margin-bottom: 10px;
}

.column-button {
  width: 50px;
  height: 40px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 20px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.column-button:hover:not(:disabled) {
  background-color: #45a049;
}

.column-button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

.column-button.active {
  animation: pulse 1.5s infinite;
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.1); }
  100% { transform: scale(1); }
}

.grid {
  background-color: #3498db;
  border-radius: 10px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.row {
  display: flex;
  gap: 5px;
}

.cell {
  width: 60px;
  height: 60px;
  background-color: white;
  border-radius: 50%;
  border: 2px solid #2980b9;
  transition: background-color 0.3s;
}

.cell.player1 {
  background-color: #e74c3c;
}

.cell.player2 {
  background-color: #f1c40f;
}

.game-id {
  margin-top: 10px;
  font-size: 16px;
  color: #555;
}